  box-shadow: 0 12px 30px -10px rgba(0, 0, 0, 0.3),
        0 10px 20px -5px rgba(0, 0, 0, 0.2);
  background-color: rgba(255, 255, 255, 0.05);
  border-color: hsl(var(--primary) / 0.5);
